<template>
    <div class="input-group">
        <input type="text" class="form-control" :placeholder="placeholder" v-model="value" readonly>
        <div class="input-group-append">
            <button class="btn btn-outline-secondary bg-h-main-color inputHasCopy" type="button" style="border: 1px solid #ced4da;"
                    data-toggle="tooltip" data-placement="top" :title="$t('text.clickToCopy')" @click="copy">
                <font-awesome-icon icon="copy" />
            </button>
        </div>
    </div>
</template>

<script>
    import copy from 'copy-to-clipboard';

    export default {
        props: ['placeholder', 'value'],

        data() {
            return {

            }
        },
        methods: {
            copy() {
                copy(this.value);
                $('.inputHasCopy').attr('title', this.$i18n.t('text.copied'))
                    .tooltip('_fixTitle')
                    .tooltip('show');
            }
        },
        computed: {

        },
        watch: {

        },
        components: {

        },
        mounted() {
            let self = this;
            $('[data-toggle="tooltip"]').tooltip();
            $('.inputHasCopy').on('hidden.bs.tooltip', function () {
                $('.inputHasCopy').attr('title', self.$i18n.t('text.clickToCopy'))
                    .tooltip('_fixTitle')
            });
        }
    }
</script>

